































import AddProjectModal from "@/components/project/AddProjectModal.vue";
import ProjectReportModal from "@/components/project/ProjectReportModal.vue";
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {ProjectReportModal, AddProjectModal},
})
export default class ProjectManagerView extends Vue {
  displayAddModal: boolean = false;
  displayReportModal: boolean = false;
  searchMonth = [new Date(), new Date()];
  mockData = [
    {
      name: "Project 1",
      sequenceName: "Sequence A",
      created: "2019/01/08",
    },
    {
      name: "Project 2",
      sequenceName: "Sequence B",
      created: "2019/05/03",
    },
    {
      name: "Project 3",
      sequenceName: "Sequence C",
      created: "2018/12/08",
    },
    {
      name: "Project 4",
      sequenceName: "Sequence D",
      created: "2019/03/18",
    },
    {
      name: "Project 5",
      sequenceName: "Sequence E",
      created: "2014/05/22",
    },
  ];

  addProject(project: any): void {
    this.mockData.push(project);
  }

  load(project: any): void {
    this.$confirm(`Are you sure ?`, "Load project", {
      confirmButtonText: "save",
      cancelButtonText: "cancel",
    });
  }

  report(project: any): void {
    this.displayReportModal = true;
  }

  remove(project: any): void {
    this.$confirm(`Are you sure delete ${project.row.name} ?`, "Delete project", {
      confirmButtonText: "save",
      cancelButtonText: "cancel",
    })
      .then(() => {
        this.mockData.splice(project.$index, 1);
        this.$message({
          type: "success",
          message: "Success!",
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "Cancel",
        });
      });
  }
}
